.card-container {
  width: 170px;
  /* height: 200px; */
  padding: 0;
  margin: 1rem;
  position: relative;
  border-radius: 5px;
}

#white {
  background: hsl(0, 0%, 98%);
  color: black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
#dark {
  background: hsl(198, 19%, 24%);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.card-container > .image {
  position: relative;
  width: 100%;
  height: 100px;
  object-fit: cover;
  z-index: 2;
  /* justify-self: flex-start; */
  border-radius: 5px 5px 0px 0px;
}

.card-container_content {
  padding: 1rem;
  font-size: 12px;
  /* color: white; */
}

.card-container_content > .property_list {
  list-style-type: none;
}
